<template>
  <div class="com-radio" @click="$emit('click')">
    <span class="com-radio__input">
      <input type="radio" class="com-radio__control" :disabled="isDisabled" v-model="currentValue">
      <i class="radio-icon blue"></i>
    </span>
  </div>
</template>
<script>
import findParent from '../mixins/find-parent'
export default {
  name: 'com-radio',
  props: {
    name: null,
    value: null,
    isDisabled: Boolean
  },
  mixins: [ findParent ],
  computed: {
    currentValue: {
      get() {
        return this.parent ? this.parent.value : this.value
      },
      set(val) {
        (this.parent || this).$emit('input', val)
      }
    }
  },
  methods: {
    check() {
      console.log(123)
    }
  }
}
</script>

<style scoped lang='less'>
@import "../../assets/css/var";
.com-radio {
  display: inline-block;
  height: 18px;
  line-height: 18px;
  overflow: hidden;
  user-select: none;
  .com-radio__input {
    display: inline-block;
    box-sizing: border-box;
    height: 18px;
    width: 18px;
    position: relative;
    font-size: 20px;
    .com-radio__control {
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0;
      margin: 0;
      width: 100%;
      height: 100%;
    }
    .radio-icon {
      display: inline-block;
      width: 18px;
      height: 18px;
      border-radius: 100%;
      pointer-events: none;
    }
    .check {
      border-radius: 100%;
      border: 1px solid#9B9B9B;
    }
    .blue {
      background: url("../../assets/images/ic_choose_blue.svg") no-repeat center;
      background-size: 20px 20px;
    }
    .white {
      background: url("../../assets/images/ic_choose_white.svg") no-repeat
        center;
      background-size: 20px 20px;
    }
  }
}
</style>
